.about-wrapper {}


.about__description-wrapper {
    width: 670px;
    align-content: flex-end; }

.about__description {
    width: 610px; }

.about__description-item {
    padding-bottom: 10px; }

.about__description-image {
    display: block;
    max-width: 100%;
    height: auto; }

.about__image_left {
    margin-left: -70px; }

.about__complect-wrapper {
    display: flex;
    justify-content: center;

    &::before {
        content: "";
        position: absolute;
        left: 30%;
        width: 940px;
        height: 186px;
        background: #F4F4F4;
        border-radius: 700px;
        z-index: -1; }

    &::after {
        content: "";
        background: url("/img/about/image3.svg") no-repeat;
        position: relative;
        top: -11px;
        width: 12em;
        height: 12em; } }

.about__complect-text {
    width: 517px;
    height: 210px;
    padding-top: 22px;
    z-index: 1; }

.about__complect-title {
    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 30px;
    line-height: 40px;
    text-transform: uppercase;
    color: #FF7F08; }

.about__complect-description {
    width: 517px;
    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    line-height: 30px;
    color: #000000; }

.about__complect-date {
    padding-top: 30px;
    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 55px;
    color: #9F9F9F; }

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box; }
.container {
    width: 1200px;
    margin: auto; }

.center {
    display: flex;
    justify-content: center; }

.row {
    display: flex;
    flex-flow: wrap row; }

.column {
    display: flex;
    flex-flow: column wrap; }

@font-face {
    font-family: 'Roboto';
    src: url("/fonts/Roboto-Regular.ttf") format('truetype');
    font-weight: normal;
    font-style: normal; }

@font-face {
    font-family: 'Roboto';
    src: url("/fonts/Roboto-Bold.ttf") format('truetype');
    font-weight: bold;
    font-style: normal; }

@font-face {
    font-family: 'Montserrat';
    src: url('/fonts/Montserrat-Light.ttf') format('truetype');
    font-weight: normal;
    font-style: normal; }

@font-face {
    font-family: 'Montserrat';
    src: url("/fonts/Montserrat-Bold.ttf") format('truetype');
    font-weight: bold;
    font-style: normal; }


.small-menu {
    font-family: Roboto, sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 19px; }

.small {
    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 25px;
    color: #000000; }


.header {
    background: #FF7F08;
    display: flex;
    align-items: center; }

.main-menu {
    width: 100%;
    display: flex;
    padding: 0 50px 0 50px; }

.menu-wrapper {
    list-style: none;
    display: flex;
    margin: 20px 0 5px 0; }

.menu__item {
    display: flex;
    align-items: center;
    height: 50px;
    border-left: 1px #FFFFFF solid;
    padding: 0 1.5em 0 1.5em;
    text-align: center; }

.menu__item-link {
    width: 140px;
    text-decoration: none;
    color: #FFFFFF; }

.subheader {
    display: flex;
    margin: 30px 0 25px 0;
    justify-content: space-between; }
.subheader__logo-wrapper {
    display: flex;
    align-items: center; }
.subheader__logo {
    margin-right: 10px; }

.subheader__description {
    display: flex;
    width: 250px;
    padding: 5px 0 5px 10px;
    color: #FFFFFF;
    border-left: 1px #FFFFFF solid;
    align-items: center; }

.subheader__logo-payment {
    display: flex;
    align-items: center;
    margin-left: 30px; }

.subheader__contact {
    width: 300px;
    align-items: center; }

.contact__phone-link {
    text-decoration: none;
    color: #547AB7;
    font-size: 20px;
    cursor: pointer;

    &:hover {
        border-bottom: 2px #547AB7 solid;
        margin: -1px; } }


.contact__phone-title {
    color: #FFFFFF;
    font-size: 14px;
    padding: 5px 0; }




@media (max-width: 1600px) and (min-width: 1400px) {
    .about__complect-wrapper::before {
        left: 25%; } }

@media (max-width: 1400px) and (min-width: 1200px) {
    .container {
        padding-left: 2em;
        padding-right: 2em; }
    .about__complect-wrapper::before {
        left: 20%; } }

@media (max-width: 1200px) and (min-width: 1000px) {
    .container {
        padding-left: 2em;
        padding-right: 2em;
        width: 1024px; }
    .about__description-wrapper {
        width: 510px; }
    .main-menu {
        padding: 0; }
    .about__complect-wrapper::before {
        left: 14%;
        width: 80%;
        height: 235px; }
    .about__image_left {
        margin-left: -40px; }
    .about__complect-text {
        width: 350px; } }


@media (max-width: 1024px) {
    .container {
        width: 1024px; }

    .about__complect-date {
        padding: 0; } }

.slider__image {
    display: block;
    max-width: 100%;
    height: auto; }
